/* credits: https://codepen.io/aaroniker/pen/wvvKKeg */

.loader-container {
  min-height: 30vh;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background: #1c212e; */
  background: #F1ECC3;
  font-family: "Roboto", Arial;
}


.loader {
  width: 200px;
  height: 140px;
  position: relative;
}

.loader:before,
.loader:after {
  content: "";
  position: absolute;
  bottom: 8px;
  width: 120px;
  top: 80%;
  box-shadow: 0 16px 12px var(--shadow);
  transform: rotate(-6deg);
}

.loader:before {
  left: 4px;
}

.loader::after {
  right: 4px;
}

.loader div {
  width: 100%;
  height: 100%;
  border-radius: 13px;
  position: relative;
  z-index: 1;
  perspective: 600px;
  box-shadow: 0 4px 6px var(--shadow);
  background-image: var(--background);
}

.loader div ul {
  list-style: none;
  position: relative;
}

.loader div ul li {
  --r: 180deg;
  --o: 0;
  --c: var(--page);
  position: absolute;
  top: 10px;
  left: 10px;
  transform-origin: 100% 50%;
  color: var(--c);
  opacity: var(--o);
  transform: rotateY(var(--r));
  animation: var(--duration) ease infinite;
}

.loader div ul li:first-child {
  opacity: 1;
  transform: rotateY(0deg);
}

.loader div ul li:nth-child(2) {
  color: var(--page-fold);
  animation-name: page-2;
}

.loader div ul li:nth-child(3) {
  color: var(--page-fold);
  animation-name: page-3;
}

.loader div ul li:nth-child(4) {
  color: var(--page-fold);
  animation-name: page-4;
}

.loader div ul li:nth-child(5) {
  color: var(--page-fold);
  animation-name: page-5;
}

.loader div ul li:last-child {
  opacity: 1;
}

.loader div ul li svg {
  width: 90px;
  height: 120px;
  display: block;
}

.loader span {
  display: block;
  left: 0;
  right: 0;
  top: 100%;
  margin-top: 20px;
  text-align: center;
  color: #57837B;
  font-size: 25px;
}


@keyframes page-2 {
  0% {
    transform: rotateY(180deg);
    opacity: 0;
  }

  20% {
    opacity: 1;
  }

  35%,
  100% {
    opacity: 0;
  }

  50%,
  100% {
    transform: rotateY(0deg);
  }
}

@keyframes page-3 {
  15% {
    transform: rotateY(180deg);
    opacity: 0;
  }

  35% {
    opacity: 1;
  }

  50%,
  100% {
    opacity: 0;
  }

  65%,
  100% {
    transform: rotateY(0deg);
  }
}

@keyframes page-4 {
  30% {
    transform: rotateY(180deg);
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  65%,
  100% {
    opacity: 0;
  }

  80%,
  100% {
    transform: rotateY(0deg);
  }
}

@keyframes page-5 {
  45% {
    transform: rotateY(180deg);
    opacity: 0;
  }

  65% {
    opacity: 1;
  }

  90%,
  100% {
    opacity: 0;
  }

  95%,
  100% {
    transform: rotateY(0deg);
  }
}
